@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}

.gameBlock {
  background: use(--template-page-joinBox-background);
  overflow: hidden;
  border-radius: 0;
  width: 100%;
  height: 100%;
  display: flex;
}

.statisticsWrapper {
  padding-top: 26px;
}

.gameWrapper {
  width: 100%;
  min-height: 400px;
  position: relative;

  display: grid;

  @media (max-width: 900px) {
    zoom: 0.8;
  }

  @media (max-width: 600px) {
    zoom: 0.5;
  }

  @media (max-width: 375px) {
    zoom: 0.3;
  }
}

.gameCell {
  height: 100%;
  width: 100%;
}

.gameFooter {
  padding: 10px;

  font-weight: 500;

  & input {
    font-weight: 500;
  }
}
