@import "sass/index";

.root {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  font-family: "Gilroy";
  font-size: 0.875rem;
  line-height: 1.0625rem;
  text-transform: uppercase;

  opacity: 0.8;
  color: use(--template-page-joinBox-text);
  font-weight: bold;

  margin: 0 5px;
  cursor: pointer;

  transition: opacity 0.3s;
}

.disableText {
  opacity: 0.3;
}

.switch {
  &Track {
    height: 14px;
    width: 28px;
  }
  &Toggle {
  }
}

.switchWrapper {
  margin: 0 10px;
}
