$rocketAnimationTime: 1000;
$fireAnimationTime: 500;

$frameSteps: 14;

$preStart: preStart 0.2s ease-in infinite;
$fly: fly #{$rocketAnimationTime}ms ease-in-out forwards;
$flyShadow: flyShadow #{$rocketAnimationTime}ms ease-in-out forwards;
$explosion: explosion #{$fireAnimationTime}ms steps(#{$frameSteps}) forwards;
$burning: burning #{$rocketAnimationTime}ms ease-in-out forwards;

:export {
  #{"rocketAnimationTime"}: $rocketAnimationTime;
  #{"fireAnimationTime"}: $fireAnimationTime;
}

@keyframes preStart {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(5%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes fly {
  0% {
    transform: translateY(0%) translateX(0);
  }
  50% {
    transform: translateX(0);
  }
  99% {
    opacity: 1;
  }
  100% {
    transform: translateY(-300px);
    opacity: 0;
  }
}

@keyframes flyShadow {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes burning {
  0% {
    transform: scaleY(0) translateY(0%);
  }
  40% {
    transform: scaleY(0) translateY(0%);
  }
  99% {
    opacity: 1;
  }
  100% {
    transform: scaleY(2) translateY(-150px);
    opacity: 0;
  }
}

@keyframes explosion {
  0% {
    background-position: center 3504px;
  }
  99% {
    opacity: 1;
  }
  100% {
    background-position: center -50px;
    opacity: 0;
  }
}

.rocket {
  max-width: 100px;
  object-fit: cover;

  position: absolute;
  bottom: 50px;
  animation: $preStart;

  transition: transform 2s;
}

.flying {
  animation: $fly;
}

.explosion {
  position: absolute;
  background-image: url(/assets/limbo/img/explosion.png);
  width: 200px;
  height: 200px;
}

.burn {
  animation: $explosion;
  display: block;
}

.noBurn {
  display: none;
}

.fire {
  width: 19px;
  height: 90px;
  object-fit: cover;

  position: absolute;
  bottom: -45px;

  transform-origin: 50% 0;
  transform: scaleY(0);
}

.leftFire {
  margin-left: -20px;
}

.rightFire {
  margin-left: 4px;
}

.burning {
  animation: $burning;
}

.shadow {
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(2, 29, 34, 0.6) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  width: 130px;
  height: 12px;

  position: absolute;
  bottom: 10px;
  animation: $preStart;
}

.flyShadow {
  animation: $flyShadow;
}
