@import "sass/index";

.root {
  width: fit-content;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  grid-gap: 10px;

  align-items: center;
  margin: 20px auto;
  @media (max-width: 600px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media (max-width: 440px) {
    grid-template-columns: 1fr;

    & > div {
      grid-row: 2;
    }
  }
}

.button {
  border-radius: 25px;
  margin: 20px 0;
  min-width: 225px;
  padding: 13px;

  color: white;
  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.14) !important;
  background: use(--template-gameCell-gameButtons-dice) !important;

  font-family: "Gilroy";
  letter-spacing: 1px;
  font-size: 1.375rem;
  line-height: 1.6875rem;
  text-transform: uppercase;
}
