@import "sass/index";

.root {
  height: 100;
  width: 100%;
  position: absolute;
  top: 20%;
  z-index: 3;
}

.payText {
  width: 100%;
  text-align: center;
  font-size: 125px;
  font-family: Gilroy, Arial, san-serif;
  font-weight: bold;
  line-height: 100%;
  margin: 0;

  color: use(--subsidiary-green);
  transition: color 1s;
}

.lose {
  color: use(--subsidiary-red);
}
