@import "sass/index";

.root {
  width: 100%;
  height: 500px;
  padding: 50px 8px;
  top: 0;
  left: 0;

  background: url(/assets/limbo/img/space.svg);
  background-position: bottom 50% center;
  background-repeat: no-repeat;
  background-size: cover;

  display: flex;
  justify-content: center;
  position: relative;
}

.footer {
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 17.36%,
    use(--template-page-joinBox-background) 57.19%
  );
  width: 100%;
  height: 240px;

  position: absolute;
  bottom: -105px;
  z-index: 0;
}
