@import "sass/index";

$limboInputHeight: 40px;
$buttonMargin: 4px;

.root {
  display: grid;
  grid-template-columns: minmax(320px, 1fr) 170px;
  grid-gap: 10px;
  gap: 10px;

  width: 500px;
  flex-grow: 1;

  @media (max-width: 520px) {
    width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
  }
}

.amountWrapper {
  position: relative;
}

.buttonWrapper {
  display: flex;
  align-items: center;
  height: 40px;
  @media (max-width: 520px) {
    display: none;
  }
}

.mobileButtonWrapper {
  display: none;
  align-items: center;
  height: 32px;
  margin-top: 5px;
  width: 100%;
  @media (max-width: 520px) {
    display: flex;
  }
}

.button {
  height: 100%;
  background: use(--template-anyContent-colors-third);
  color: use(--template-page-joinBox-text);
  border-radius: 0;
  margin: 0 1px;

  font-size: 0.75rem;
  width: 40px;
  min-width: 0;

  @media (max-width: 520px) {
    width: 100%;
  }
}

.buttonLeft {
  border-radius: 6px 0px 0px 6px;
}

.buttonRight {
  border-radius: 0px 6px 6px 0px;
}

