@import "sass/index";

$limboInputHeight: 40px;

.wrapper {
  width: 100%;
  position: relative;
  overflow: hidden;

  & > div > label {
    line-height: 1.0625rem;
    font-size: 1rem;

    font-family: "Gilroy";
    font-weight: bold;

    color: use(--template-page-joinBox-text);
    opacity: 0.8;
  }
}

.root {
  border: 1px solid use(--template-gameBlock-background) !important;
  background: var(--template-page-background);
  border-radius: 8px;
  height: $limboInputHeight;
  color: use(--template-page-joinBox-text) !important;
  box-shadow: none !important;
  width: 100%;
}

.disabled {
  color: use(--template-page-joinBox-text, 0.6);
}

.coinImg {
  position: static;
}

.infinityImg {
  color: use(--template-page-joinBox-text);
  opacity: 0.2;
}

.usdAdornment {
  margin-left: 3px;
}
