@import "sass/index";

$paddingSize: 10px;

.bigLine {
  background: $mui-primary-main;
  width: calc(100% + #{$paddingSize * 2});
  height: 10px;
  margin-top: $paddingSize * 2;
  margin-left: -1 * $paddingSize;
}

.line {
  background: use(--template-page-background);
  width: calc(100% + 20px);
  height: 1px;
  margin-left: -10px;
}

.box {
  width: 100%;
  max-width: 900px;
  margin: 0 auto 20px;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.options {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  margin-bottom: 15px;
}
